.circle {
     height: 150px;
     width: 150px;
     cursor: pointer;
     background-color: #fff;
     border-radius: 50%;
     display: flex;
     margin: 15px 0;
     box-shadow: 1px 2px 5px 1px #ced6e0;
}
.round {
     border: 3px chocolate;
     border-style: double;
}
.itemName {
     margin: auto;
     padding: 0 5px;
     font-size: 12px;
     text-decoration: none;
     color: #000000;
}
.linkItem:hover {
     text-decoration: none;
     color: #000000;
}
